import React, {useEffect, useRef, useState} from 'react';
import './index.css';
import {Button, List, message, Modal, Skeleton, Tag} from "antd";
import {useMount, useUnmount} from "ahooks";
import axios from "axios";


function SearchBar (props) {
    const {setHotWords, hotWords} = props
    const [inputString, setInputString] = useState('');
    const [relevantWords, setRelevantWords] = useState([]);
    const inputElement = useRef();

    const onBlur = (e) => {
        let hotWords = document.getElementById('hotwords');
        hotWords.style.transition = 'filter 1s';
        hotWords.style.filter = 'blur(0px)';
        let bg = document.getElementById('bg');
        bg.style.filter = 'blur(0px)';
    }
    const handleSubmit = () => {
        const str = inputElement.current.value;
        if(!!str){
            setHotWords(pre => [...pre, str])
            setInputString('')
            inputElement.current.value = ''

        }
    }

    const handleChange = async e => {
        if(e.target.value !== ""){
            setInputString(e.target.value);
        }
        else{
            let historyWords = localStorage.getItem('historyWords');
            const arr = [...new Set(JSON.parse(historyWords))];
            if(historyWords){
                setRelevantWords([...new Set(arr)]);
            }
        }
    }

    useMount(() => {
        document.addEventListener('click', ev =>{
            setRelevantWords([]);
        });
    })
    return (
        <div className="search bar2">
            <div className={'form'} id={'indexSearchBox'}>
                <input type="text"
                       placeholder="请输入你要添加的选项..."
                       // onFocus={onFocus}
                       onBlur={onBlur}
                       id={'indexInput'}
                       ref={inputElement}
                       onChange={handleChange}
                       onKeyDown={(e) => {
                           if(e.code === 'Enter'){
                               handleSubmit();
                           }
                       }}
                       onClick={e => e.nativeEvent.stopImmediatePropagation()}
                       autoComplete="off"
                />
                    <button type="submit"
                            id={'indexButton'}
                            onClick={handleSubmit}
                    />
                <Button type={'primary'} style={{position: 'absolute', left: '70%'}} onClick={() => {
                    Modal.info({content:<div>
                            <div>您随机选取了<Tag color={'green'}>{hotWords[Math.floor(Math.random() * hotWords.length)]}</Tag></div><br/><div>全部的选项为：{hotWords.join('、')}</div>
                        </div>, icon: null,style: {width: '80%', height: '80vh'}})
                }}>随机选择</Button>
                {
                    relevantWords.length === 0 ? "" :(<div id={'word-complete'}><List
                                                            size="small"

                                                            dataSource={relevantWords}
                                                            split={false}
                                                            onClick={e => {
                                                                e.nativeEvent.stopImmediatePropagation()
                                                                const str = e.target.textContent;
                                                                inputElement.current.value = str;
                                                                setInputString(str);
                                                                handleSubmit();
                                                            }}
                                                            renderItem={(item, index) =>
                                                                <List.Item style={{borderRadius:`${index === 0 ? '10px 10px 0 0':'0 0 0 0'}`}} className={'word-item'}>{item}</List.Item>}/>
                        {inputElement.current.value !== '' ? <div/>:<Button style={{border:'0', float:'right', fontSize:'.8rem'}}
                                                                            onClick={e => {
                                                                                e.nativeEvent.stopImmediatePropagation();
                                                                                localStorage.removeItem('historyWords')
                                                                                setRelevantWords([]);
                                                                            }}
                        >清除历史记录</Button>}
                    </div>)
                }
            </div>

        </div>
    );
}

export default SearchBar;